import { Component, OnInit } from '@angular/core';
import { Platform } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { EventbusService } from './core/eventbus.service';
import { UtilityService } from './core/utility.service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent implements OnInit {
  public selectedIndex = 0;
  public appPages = [
    {
      title: 'Appointments',
      url: '/tabs/appointments',
      icon: 'calendar',
    },
    {
      title: 'Park Near To Me',
      url: '/tabs/park-near-me',
      icon: 'leaf',
    },
    {
      title: 'My Product',
      url: '/tabs/my-products',
      icon: 'leaf',
    },
    {
      title: 'Cart',
      url: '/tabs/cart',
      icon: 'leaf',
    },
    {
      title: 'QR Generation',
      url: '/tabs/qr-code',
      icon: 'qr-code',
    },
    {
      title: 'Payments',
      url: '/tabs/subscription-transactions',
      icon: 'card',
    },
    {
      title: 'Settings',
      url: '/folder/Favorites',
      icon: 'settings',
    },
    {
      title: 'Logout',
      url: '',
      icon: 'log-out',
    },
  ];
  public labels = ['Family', 'Friends', 'Notes', 'Work', 'Travel', 'Reminders'];

  constructor(
    private translate: TranslateService,
    public utility: UtilityService,
    private eventBus: EventbusService,
    private platform: Platform
  ) {
    this.translate.setDefaultLang('en');
    this.utility.setSideMenuUserDetail();
  }

  ngOnInit() {
    const path = window.location.pathname.split('folder/')[1];
    if (path !== undefined) {
      this.selectedIndex = this.appPages.findIndex(
        (page) => page.title.toLowerCase() === path.toLowerCase()
      );
    }
    this.eventBus.cred$.subscribe((res) => {
      if (res) {
        this.utility.userEmail = res.userEmail;
        this.utility.username = res.username;
        this.utility.userImage = res.userImage;
        this.utility.dogFooterImage = res.dogFooterImage;
      }
    });
    this.handleSidemnuEvent();
  }
  logout(p) {
    if (p.title.toLowerCase() === 'logout') {
      localStorage.clear();
      this.utility.navigateRoot('/login');
    }
  }

  handleSidemnuEvent() {
    this.eventBus.sideMenu$.subscribe((res) => {
      if (res == 'service-provider') {
        this.appPages = [
          {
            title: 'Appointments',
            url: '/tabs/appointments',
            icon: 'calendar',
          },
          {
            title: 'Settings',
            url: '/folder/Favorites',
            icon: 'settings',
          },
          {
            title: 'Logout',
            url: '',
            icon: 'log-out',
          },
        ];
      }
    });
  }
}
