import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {
  LoadingController,
  NavController,
  ToastController,
} from '@ionic/angular';
import { AlertController } from '@ionic/angular';
import { environment } from 'src/environments/environment';

import { HttpClient, HttpHeaders, HttpBackend } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { EventbusService } from './eventbus.service';
@Injectable({
  providedIn: 'root',
})
export class UtilityService {
  isLoading = false;
  baseUrl = environment.apiUrl + 'service/';
  addPet = 'add';
  updatePet = 'update/{petId}';
  deletePet = 'delete/{petId}';
  getServiceDetail = 'details';
  getSinglePet = 'get/{petId}';
  headers;
  username;
  userImage;
  dogFooterImage;
  userEmail;
  loading;
  refreshDetail = true;
  constructor(
    private http: HttpClient,
    private bHttp: HttpBackend,
    private router: Router,
    private loadingController: LoadingController,
    private alertController: AlertController,
    private navController: NavController,
    private toastController: ToastController,
    public eventBus: EventbusService
  ) {
    this.headers = new HttpHeaders({
      authorization: this.getToken(),
    });
  }

  navigate(url) {
    this.navController.navigateForward(url);
    // this.router.navigateByUrl(url);
  }

  getToken() {
    return this.getLocal('token');
  }

  getLocal(key) {
    return localStorage.getItem(key);
  }
  setLocal(key, value) {
    localStorage.setItem(key, value);
  }

  getUserId() {
    const user = JSON.parse(localStorage.getItem('userObj'));
    return user['_id'];
  }
  getImageUrl(image) {
    console.log(image);
    return JSON.parse(image).map((v) => {
      v = environment.apiUrl + 'uploads/' + v;
      return v;
    });
  }
  getPetImageUrl(image) {
    console.log(image);

    return environment.apiUrl + 'uploads/' + image;
  }
  async showAlert(msg) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Message',
      message: msg,
      buttons: [
        // {
        //   text: 'Cancel',
        //   role: 'cancel',
        //   cssClass: 'secondary',
        //   handler: (blah) => {
        //     console.log('Confirm Cancel: blah');
        //   },
        // },
        {
          text: 'OK',
          handler: () => {
            console.log('Confirm Okay');
          },
        },
      ],
    });

    await alert.present();
  }

  async confirmationAlert(cb) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Confirmation',
      message: 'Are you sure want to delete this?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
            cb(false);
          },
        },
        {
          text: 'OK',
          handler: () => {
            console.log('Confirm Okay');
            cb(true);
          },
        },
      ],
    });

    await alert.present();
  }

  goBack() {
    this.navController.back();
  }
  navigateRoot(url) {
    this.navController.navigateRoot(url);
  }

  showLoader() {
    this.loadingController
      .create({
        message: 'Please wait...',
      })
      .then((res) => {
        res.present();
      });
  }

  // Hide the loader if already created otherwise return error
  hideLoader() {
    this.loadingController
      .dismiss()
      .then((res) => {
        console.log('Loading dismissed!', res);
      })
      .catch((error) => {
        console.log('error', error);
        this.loadingController.dismiss();
      });
  }
  pop() {
    this.navController.pop();
  }

  async presentToastWithOptions(msg) {
    const toast = await this.toastController.create({
      header: 'Message',
      message: msg,
      position: 'top',
      buttons: [
        {
          text: 'Done',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          },
        },
      ],
    });
    await toast.present();
  }

  async present() {
    this.isLoading = true;
    return await this.loadingController
      .create({
        message: 'Please wait...',
        // duration: 5000,
      })
      .then((a) => {
        a.present().then(() => {
          console.log('presented');
          if (!this.isLoading) {
            a.dismiss().then(() => console.log('abort presenting'));
          }
        });
      });
  }

  async dismiss() {
    this.isLoading = false;
    return await this.loadingController
      .dismiss()
      .then(() => console.log('dismissed'));
  }
  async customAlert(btn1 = 'Cancel', btn2 = 'Ok', msg, cb) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Message',
      message: msg,
      buttons: [
        {
          text: btn1,
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
            cb(false);
          },
        },
        {
          text: btn2,
          handler: () => {
            console.log('Confirm Okay');
            cb(true);
          },
        },
      ],
    });

    await alert.present();
  }

  async subscriptionAlert(
    btn1 = 'Cancel',
    btn2 = 'Ok',
    btn3 = 'Cancel',
    msg,
    cb
  ) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Message',
      message: msg,
      buttons: [
        {
          text: btn1,
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
            cb(false);
          },
        },
        {
          text: btn2,
          handler: () => {
            console.log('Confirm Okay');
            cb(true);
          },
        },
        {
          text: btn3,
          handler: () => {
            console.log('Confirm null');
            cb(null);
          },
        },
      ],
    });

    await alert.present();
  }

  setSideMenuUserDetail() {
    const user = JSON.parse(this.getLocal('userObj')) || {};
    this.userEmail = user.email;
    this.userImage = user.image ? this.getPetImageUrl(user.image) : undefined;
    this.dogFooterImage = user.dogFooterImage
      ? this.getPetImageUrl(user.dogFooterImage)
      : undefined;

    this.username = user.firstname + ' ' + user.lastname;
    console.log(this.username);
    let data = {
      userEmail: this.userEmail,
      userImage: this.userImage,
      username: this.username,
      dogFooterImage: this.dogFooterImage,
    };
    console.log(data, 'data');
    this.eventBus.cred.next(data);
  }

  async prompt(msg, cb) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Message',
      message: msg,
      buttons: [
        {
          text: 'Ok',
          handler: () => {
            console.log('Confirm Okay');
            cb(true);
          },
        },
      ],
    });

    await alert.present();
  }

  getRole() {
    return JSON.parse(this.getLocal('userObj')).role;
  }
}
